"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Separator } from "@/components/ui/separator";

interface Submission {
  applicationId: string;
  fullName: string;
  email: string;
  phone: string;
  itemDescription: string;
  estimatedValue: number;
  address: string;
  photoUrl: string;
  status: "pending" | "accepted" | "rejected";
  submittedAt: string;
  reviewedAt: string | null;
  reviewedBy: string | null;
  pawnValue: number | null;
  rejectionReason?: string;
}

export default function AdminDetailPage({ params }: { params: { applicationId: string } }) {
  const router = useRouter();
  const [submission, setSubmission] = useState<Submission | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [processing, setProcessing] = useState(false);
  const [pawnValue, setPawnValue] = useState("");
  const [rejectionReason, setRejectionReason] = useState("");

  useEffect(() => {
    fetchSubmission();
  }, [params.applicationId]);

  const fetchSubmission = async () => {
    try {
      const response = await fetch(`/api/admin/submissions/${params.applicationId}`);
      if (!response.ok) {
        throw new Error("Pengajuan tidak ditemukan");
      }
      const data = await response.json();
      setSubmission(data.submission);
      if (data.submission.pawnValue) {
        setPawnValue(data.submission.pawnValue.toString());
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : "Terjadi kesalahan");
    } finally {
      setLoading(false);
    }
  };

  const handleAccept = async () => {
    if (!pawnValue || parseFloat(pawnValue) <= 0) {
      setError("Nilai gadai harus diisi dengan angka yang valid");
      return;
    }

    setProcessing(true);
    setError("");

    try {
      const response = await fetch(`/api/admin/submissions/${params.applicationId}/accept`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          pawnValue: parseFloat(pawnValue),
        }),
      });

      if (!response.ok) {
        const data = await response.json();
        throw new Error(data.error || "Gagal menerima pengajuan");
      }

      // Refresh submission data
      await fetchSubmission();
      
      // Show success message and redirect after delay
      setTimeout(() => {
        router.push("/admin");
      }, 2000);

    } catch (err) {
      setError(err instanceof Error ? err.message : "Terjadi kesalahan");
    } finally {
      setProcessing(false);
    }
  };

  const handleReject = async () => {
    if (!rejectionReason.trim()) {
      setError("Alasan penolakan harus diisi");
      return;
    }

    setProcessing(true);
    setError("");

    try {
      const response = await fetch(`/api/admin/submissions/${params.applicationId}/reject`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          reason: rejectionReason,
        }),
      });

      if (!response.ok) {
        const data = await response.json();
        throw new Error(data.error || "Gagal menolak pengajuan");
      }

      // Refresh submission data
      await fetchSubmission();
      
      // Show success message and redirect after delay
      setTimeout(() => {
        router.push("/admin");
      }, 2000);

    } catch (err) {
      setError(err instanceof Error ? err.message : "Terjadi kesalahan");
    } finally {
      setProcessing(false);
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("id-ID", {
      style: "currency",
      currency: "IDR",
      minimumFractionDigits: 0,
    }).format(amount);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString("id-ID", {
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "pending":
        return <Badge variant="secondary">Menunggu Review</Badge>;
      case "accepted":
        return <Badge className="bg-green-100 text-green-800 hover:bg-green-100">Diterima</Badge>;
      case "rejected":
        return <Badge variant="destructive">Ditolak</Badge>;
      default:
        return <Badge variant="outline">{status}</Badge>;
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900 mx-auto mb-4"></div>
          <p className="text-gray-600">Memuat data...</p>
        </div>
      </div>
    );
  }

  if (!submission) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardHeader className="text-center">
            <CardTitle className="text-red-600">Pengajuan Tidak Ditemukan</CardTitle>
            <CardDescription>
              Pengajuan dengan ID {params.applicationId} tidak ditemukan.
            </CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <Link href="/admin">
              <Button>Kembali ke Dashboard</Button>
            </Link>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white">
      {/* Header */}
      <header className="border-b bg-white/80 backdrop-blur-sm">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <Link href="/admin" className="text-2xl font-bold text-gray-900">
              KOKOGADAI Admin
            </Link>
            <nav className="flex gap-4">
              <Link href="/admin">
                <Button variant="outline">Dashboard</Button>
              </Link>
            </nav>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-4xl mx-auto">
          {/* Header */}
          <div className="mb-8">
            <div className="flex items-center gap-4 mb-2">
              <h1 className="text-3xl font-bold text-gray-900">Detail Pengajuan</h1>
              {getStatusBadge(submission.status)}
            </div>
            <p className="text-gray-600">ID: {submission.applicationId}</p>
          </div>

          {error && (
            <Alert className="mb-6 border-red-200 bg-red-50">
              <AlertDescription className="text-red-800">
                {error}
              </AlertDescription>
            </Alert>
          )}

          {submission.status === "accepted" && (
            <Alert className="mb-6 border-green-200 bg-green-50">
              <AlertDescription className="text-green-800">
                Pengajuan telah diterima dan sedang diproses untuk penjemputan.
              </AlertDescription>
            </Alert>
          )}

          {submission.status === "rejected" && (
            <Alert className="mb-6 border-red-200 bg-red-50">
              <AlertDescription className="text-red-800">
                Pengajuan telah ditolak. Alasan: {submission.rejectionReason}
              </AlertDescription>
            </Alert>
          )}

          <div className="grid lg:grid-cols-2 gap-8">
            {/* Applicant Information */}
            <Card>
              <CardHeader>
                <CardTitle>Informasi Pemohon</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-600">Nama Lengkap</Label>
                  <p className="text-lg font-semibold">{submission.fullName}</p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-600">Email</Label>
                  <p>{submission.email}</p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-600">Nomor Telepon</Label>
                  <p>{submission.phone}</p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-600">Alamat</Label>
                  <p>{submission.address}</p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-600">Tanggal Pengajuan</Label>
                  <p>{formatDate(submission.submittedAt)}</p>
                </div>
              </CardContent>
            </Card>

            {/* Item Information */}
            <Card>
              <CardHeader>
                <CardTitle>Informasi Barang</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-600">Deskripsi Barang</Label>
                  <p>{submission.itemDescription}</p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-600">Estimasi Nilai</Label>
                  <p className="text-lg font-semibold">{formatCurrency(submission.estimatedValue)}</p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-600">Foto Barang</Label>
                  <div className="mt-2 p-4 border-2 border-dashed border-gray-300 rounded-lg text-center">
                    <p className="text-gray-500">Foto: {submission.photoUrl}</p>
                    <p className="text-sm text-gray-400 mt-1">
                      (Dalam implementasi nyata, foto akan ditampilkan di sini)
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Review Section */}
          {submission.status === "pending" && (
            <Card className="mt-8">
              <CardHeader>
                <CardTitle>Review Pengajuan</CardTitle>
                <CardDescription>
                  Tentukan apakah pengajuan ini diterima atau ditolak
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                {/* Accept Section */}
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-green-700">Terima Pengajuan</h3>
                  <div className="space-y-2">
                    <Label htmlFor="pawnValue">Nilai Gadai yang Disetujui (Rp)</Label>
                    <Input
                      id="pawnValue"
                      type="number"
                      value={pawnValue}
                      onChange={(e) => setPawnValue(e.target.value)}
                      placeholder="Masukkan nilai gadai"
                    />
                    <p className="text-sm text-gray-500">
                      Nilai ini akan menjadi jumlah dana yang dicairkan kepada pemohon
                    </p>
                  </div>
                  <Button
                    onClick={handleAccept}
                    disabled={processing}
                    className="bg-green-600 hover:bg-green-700"
                  >
                    {processing ? "Memproses..." : "Terima Pengajuan"}
                  </Button>
                </div>

                <Separator />

                {/* Reject Section */}
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-red-700">Tolak Pengajuan</h3>
                  <div className="space-y-2">
                    <Label htmlFor="rejectionReason">Alasan Penolakan</Label>
                    <Textarea
                      id="rejectionReason"
                      value={rejectionReason}
                      onChange={(e) => setRejectionReason(e.target.value)}
                      placeholder="Jelaskan alasan penolakan pengajuan ini"
                      rows={3}
                    />
                  </div>
                  <Button
                    onClick={handleReject}
                    disabled={processing}
                    variant="destructive"
                  >
                    {processing ? "Memproses..." : "Tolak Pengajuan"}
                  </Button>
                </div>
              </CardContent>
            </Card>
          )}

          {/* Review History */}
          {submission.status !== "pending" && (
            <Card className="mt-8">
              <CardHeader>
                <CardTitle>Riwayat Review</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <p><strong>Status:</strong> {submission.status === "accepted" ? "Diterima" : "Ditolak"}</p>
                  <p><strong>Direview oleh:</strong> {submission.reviewedBy}</p>
                  <p><strong>Tanggal Review:</strong> {submission.reviewedAt ? formatDate(submission.reviewedAt) : "-"}</p>
                  {submission.pawnValue && (
                    <p><strong>Nilai Gadai:</strong> {formatCurrency(submission.pawnValue)}</p>
                  )}
                  {submission.rejectionReason && (
                    <p><strong>Alasan Penolakan:</strong> {submission.rejectionReason}</p>
                  )}
                </div>
              </CardContent>
            </Card>
          )}
        </div>
      </div>
    </div>
  );
}
